"use strict";
exports.formatDistance = void 0;

const formatDistanceLocale = {
  lessThanXSeconds: {
    standalone: {
      one: "vhem kui ks sekund",
      other: "vhem kui {{count}} sekundit",
    },
    withPreposition: {
      one: "vhem kui he sekundi",
      other: "vhem kui {{count}} sekundi",
    },
  },

  xSeconds: {
    standalone: {
      one: "ks sekund",
      other: "{{count}} sekundit",
    },
    withPreposition: {
      one: "he sekundi",
      other: "{{count}} sekundi",
    },
  },

  halfAMinute: {
    standalone: "pool minutit",
    withPreposition: "poole minuti",
  },

  lessThanXMinutes: {
    standalone: {
      one: "vhem kui ks minut",
      other: "vhem kui {{count}} minutit",
    },
    withPreposition: {
      one: "vhem kui he minuti",
      other: "vhem kui {{count}} minuti",
    },
  },

  xMinutes: {
    standalone: {
      one: "ks minut",
      other: "{{count}} minutit",
    },
    withPreposition: {
      one: "he minuti",
      other: "{{count}} minuti",
    },
  },

  aboutXHours: {
    standalone: {
      one: "umbes ks tund",
      other: "umbes {{count}} tundi",
    },
    withPreposition: {
      one: "umbes he tunni",
      other: "umbes {{count}} tunni",
    },
  },

  xHours: {
    standalone: {
      one: "ks tund",
      other: "{{count}} tundi",
    },
    withPreposition: {
      one: "he tunni",
      other: "{{count}} tunni",
    },
  },

  xDays: {
    standalone: {
      one: "ks pev",
      other: "{{count}} peva",
    },
    withPreposition: {
      one: "he peva",
      other: "{{count}} peva",
    },
  },

  aboutXWeeks: {
    standalone: {
      one: "umbes ks ndal",
      other: "umbes {{count}} ndalat",
    },
    withPreposition: {
      one: "umbes he ndala",
      other: "umbes {{count}} ndala",
    },
  },

  xWeeks: {
    standalone: {
      one: "ks ndal",
      other: "{{count}} ndalat",
    },
    withPreposition: {
      one: "he ndala",
      other: "{{count}} ndala",
    },
  },

  aboutXMonths: {
    standalone: {
      one: "umbes ks kuu",
      other: "umbes {{count}} kuud",
    },
    withPreposition: {
      one: "umbes he kuu",
      other: "umbes {{count}} kuu",
    },
  },

  xMonths: {
    standalone: {
      one: "ks kuu",
      other: "{{count}} kuud",
    },
    withPreposition: {
      one: "he kuu",
      other: "{{count}} kuu",
    },
  },

  aboutXYears: {
    standalone: {
      one: "umbes ks aasta",
      other: "umbes {{count}} aastat",
    },
    withPreposition: {
      one: "umbes he aasta",
      other: "umbes {{count}} aasta",
    },
  },

  xYears: {
    standalone: {
      one: "ks aasta",
      other: "{{count}} aastat",
    },
    withPreposition: {
      one: "he aasta",
      other: "{{count}} aasta",
    },
  },

  overXYears: {
    standalone: {
      one: "rohkem kui ks aasta",
      other: "rohkem kui {{count}} aastat",
    },
    withPreposition: {
      one: "rohkem kui he aasta",
      other: "rohkem kui {{count}} aasta",
    },
  },

  almostXYears: {
    standalone: {
      one: "peaaegu ks aasta",
      other: "peaaegu {{count}} aastat",
    },
    withPreposition: {
      one: "peaaegu he aasta",
      other: "peaaegu {{count}} aasta",
    },
  },
};

const formatDistance = (token, count, options) => {
  const usageGroup = options?.addSuffix
    ? formatDistanceLocale[token].withPreposition
    : formatDistanceLocale[token].standalone;

  let result;
  if (typeof usageGroup === "string") {
    result = usageGroup;
  } else if (count === 1) {
    result = usageGroup.one;
  } else {
    result = usageGroup.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + " prast";
    } else {
      return result + " eest";
    }
  }

  return result;
};
exports.formatDistance = formatDistance;
