"use strict";
exports.formatDistance = void 0;

// Source: https://www.unicode.org/cldr/charts/32/summary/te.html

const formatDistanceLocale = {
  lessThanXSeconds: {
    standalone: {
      one: "  ",
      other: "{{count}}   ",
    },
    withPreposition: {
      one: "",
      other: "{{count}} ",
    },
  },

  xSeconds: {
    standalone: {
      one: " ", // CLDR #1314
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  halfAMinute: {
    standalone: " ",
    withPreposition: " ",
  },

  lessThanXMinutes: {
    standalone: {
      one: "   ",
      other: "{{count}}   ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  xMinutes: {
    standalone: {
      one: " ", // CLDR #1311
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ", // CLDR #1311
      other: "{{count}} ",
    },
  },

  aboutXHours: {
    standalone: {
      one: "  ",
      other: " {{count}} ",
    },
    withPreposition: {
      one: "  ",
      other: " {{count}} ",
    },
  },

  xHours: {
    standalone: {
      one: " ", // CLDR #1308
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  xDays: {
    standalone: {
      one: " ", // CLDR #1292
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  aboutXWeeks: {
    standalone: {
      one: "  ",
      other: " {{count}} ",
    },
    withPreposition: {
      one: "  ",
      other: " {{count}} ",
    },
  },

  xWeeks: {
    standalone: {
      one: " ",
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  aboutXMonths: {
    standalone: {
      one: "  ",
      other: " {{count}} ",
    },
    withPreposition: {
      one: "  ",
      other: " {{count}} ",
    },
  },

  xMonths: {
    standalone: {
      one: " ", // CLDR #1281
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  aboutXYears: {
    standalone: {
      one: "  ",
      other: " {{count}} ",
    },
    withPreposition: {
      one: "  ",
      other: " {{count}} ",
    },
  },

  xYears: {
    standalone: {
      one: " ", // CLDR #1275
      other: "{{count}} ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  overXYears: {
    standalone: {
      one: "  ",
      other: "{{count}}  ",
    },
    withPreposition: {
      one: " ",
      other: "{{count}} ",
    },
  },

  almostXYears: {
    standalone: {
      one: "  ",
      other: " {{count}} ",
    },
    withPreposition: {
      one: "  ",
      other: " {{count}} ",
    },
  },
};

const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = options?.addSuffix
    ? formatDistanceLocale[token].withPreposition
    : formatDistanceLocale[token].standalone;

  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + "";
    } else {
      return result + " ";
    }
  }

  return result;
};
exports.formatDistance = formatDistance;
