"use strict";
exports.formatDistance = void 0;

const formatDistanceLocale = {
  lessThanXSeconds: {
    past: "{{count}}    ",
    present: "{{count}}  ",
    future: "{{count}}  ",
  },

  xSeconds: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  halfAMinute: {
    past: "  ",
    present: " ",
    future: " ",
  },

  lessThanXMinutes: {
    past: "{{count}}    ",
    present: "{{count}}  ",
    future: "{{count}}  ",
  },

  xMinutes: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  aboutXHours: {
    past: " {{count}}  ",
    present: " {{count}} ",
    future: " {{count}} ",
  },

  xHours: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  xDays: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  aboutXWeeks: {
    past: " {{count}}  ",
    present: " {{count}} ",
    future: " {{count}} ",
  },

  xWeeks: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  aboutXMonths: {
    past: " {{count}}  ",
    present: " {{count}} ",
    future: " {{count}} ",
  },

  xMonths: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  aboutXYears: {
    past: " {{count}}  ",
    present: " {{count}} ",
    future: " {{count}} ",
  },

  xYears: {
    past: "{{count}}  ",
    present: "{{count}} ",
    future: "{{count}} ",
  },

  overXYears: {
    past: "{{count}}    ",
    present: "{{count}}  ",
    future: "{{count}}    ",
  },

  almostXYears: {
    past: " {{count}}  ",
    present: " {{count}} ",
    future: " {{count}} ",
  },
};

const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (
    options?.addSuffix &&
    options.comparison &&
    options.comparison > 0
  ) {
    result = tokenValue.future.replace("{{count}}", String(count));
  } else if (options?.addSuffix) {
    result = tokenValue.past.replace("{{count}}", String(count));
  } else {
    result = tokenValue.present.replace("{{count}}", String(count));
  }

  return result;
};
exports.formatDistance = formatDistance;
