"use strict";
exports.formatDistance = void 0;

const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "   ",
    other: "  {{count}} ",
  },

  xSeconds: {
    one: "1 ",
    other: "{{count}} ",
  },

  halfAMinute: " ",

  lessThanXMinutes: {
    one: "   ",
    other: "  {{count}} ",
  },

  xMinutes: {
    one: "1 ",
    other: "{{count}} ",
  },

  aboutXHours: {
    one: " 1 ",
    other: " {{count}} ",
  },

  xHours: {
    one: "1 ",
    other: "{{count}} ",
  },

  xDays: {
    one: "1 ",
    other: "{{count}} ",
  },

  aboutXWeeks: {
    one: " 1 ",
    other: " {{count}} ",
  },

  xWeeks: {
    one: "1 ",
    other: "{{count}} ",
  },

  aboutXMonths: {
    one: " 1 ",
    other: " {{count}} ",
  },

  xMonths: {
    one: "1 ",
    other: "{{count}} ",
  },

  aboutXYears: {
    one: "  1 ",
    other: " {{count}} ",
  },

  xYears: {
    one: "1 ",
    other: "{{count}} ",
  },

  overXYears: {
    one: "  ",
    other: "  {{count}} ",
  },

  almostXYears: {
    one: "   ",
    other: " {{count}} ",
  },
};

const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", count.toString());
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return "  " + result + "";
    } else {
      return result + " ";
    }
  }

  return result;
};
exports.formatDistance = formatDistance;
