"use strict";
exports.formatDistance = void 0;

const formatDistanceLocale = {
  lessThanXSeconds: {
    one: "bir saniydn az",
    other: "{{count}} bir saniydn az",
  },

  xSeconds: {
    one: "1 saniy",
    other: "{{count}} saniy",
  },

  halfAMinute: "yarm dqiq",

  lessThanXMinutes: {
    one: "bir dqiqdn az",
    other: "{{count}} bir dqiqdn az",
  },

  xMinutes: {
    one: "bir dqiq",
    other: "{{count}} dqiq",
  },

  aboutXHours: {
    one: "txminn 1 saat",
    other: "txminn {{count}} saat",
  },

  xHours: {
    one: "1 saat",
    other: "{{count}} saat",
  },

  xDays: {
    one: "1 gn",
    other: "{{count}} gn",
  },

  aboutXWeeks: {
    one: "txminn 1 hft",
    other: "txminn {{count}} hft",
  },

  xWeeks: {
    one: "1 hft",
    other: "{{count}} hft",
  },

  aboutXMonths: {
    one: "txminn 1 ay",
    other: "txminn {{count}} ay",
  },

  xMonths: {
    one: "1 ay",
    other: "{{count}} ay",
  },

  aboutXYears: {
    one: "txminn 1 il",
    other: "txminn {{count}} il",
  },

  xYears: {
    one: "1 il",
    other: "{{count}} il",
  },

  overXYears: {
    one: "1 ildn ox",
    other: "{{count}} ildn ox",
  },

  almostXYears: {
    one: "demk olar ki 1 il",
    other: "demk olar ki {{count}} il",
  },
};

const formatDistance = (token, count, options) => {
  let result;

  const tokenValue = formatDistanceLocale[token];
  if (typeof tokenValue === "string") {
    result = tokenValue;
  } else if (count === 1) {
    result = tokenValue.one;
  } else {
    result = tokenValue.other.replace("{{count}}", String(count));
  }

  if (options?.addSuffix) {
    if (options.comparison && options.comparison > 0) {
      return result + " sonra";
    } else {
      return result + " vvl";
    }
  }

  return result;
};
exports.formatDistance = formatDistance;
