"use strict";
exports.formatDistance = void 0;

const translations = {
  about: "krlbell",
  over: "tbb mint",
  almost: "majdnem",
  lessthan: "kevesebb mint",
};

const withoutSuffixes = {
  xseconds: " msodperc",
  halfaminute: "fl perc",
  xminutes: " perc",
  xhours: " ra",
  xdays: " nap",
  xweeks: " ht",
  xmonths: " hnap",
  xyears: " v",
};

const withSuffixes = {
  xseconds: {
    "-1": " msodperccel ezeltt",
    1: " msodperc mlva",
    0: " msodperce",
  },
  halfaminute: {
    "-1": "fl perccel ezeltt",
    1: "fl perc mlva",
    0: "fl perce",
  },
  xminutes: {
    "-1": " perccel ezeltt",
    1: " perc mlva",
    0: " perce",
  },
  xhours: {
    "-1": " rval ezeltt",
    1: " ra mlva",
    0: " rja",
  },
  xdays: {
    "-1": " nappal ezeltt",
    1: " nap mlva",
    0: " napja",
  },
  xweeks: {
    "-1": " httel ezeltt",
    1: " ht mlva",
    0: " hete",
  },
  xmonths: {
    "-1": " hnappal ezeltt",
    1: " hnap mlva",
    0: " hnapja",
  },
  xyears: {
    "-1": " vvel ezeltt",
    1: " v mlva",
    0: " ve",
  },
};

const formatDistance = (token, count, options) => {
  const adverb = token.match(/about|over|almost|lessthan/i);
  const unit = adverb ? token.replace(adverb[0], "") : token;

  const addSuffix = options?.addSuffix === true;
  const key = unit.toLowerCase();
  const comparison = options?.comparison || 0;

  const translated = addSuffix
    ? withSuffixes[key][comparison]
    : withoutSuffixes[key];

  let result = key === "halfaminute" ? translated : count + translated;

  if (adverb) {
    const adv = adverb[0].toLowerCase();
    result = translations[adv] + " " + result;
  }

  return result;
};
exports.formatDistance = formatDistance;
